
import VueRouter from "vue-router";

const routes = [
    {
        path: '/',
        name: 'policy',
        redirect: '/home',
        component: () => import('../view/Policy'),
        children: [
            {
                path: 'home',
                name: 'home',
                component: () => import('../view/Policy/Home')
            },
            {
                path: 'contact',
                name: 'contact',
                component: () => import('../view/Policy/Contact')
            },
            {
                path: 'about',
                name: 'about',
                component: () => import('../view/Policy/About'),
            }
        ]
    },
    {
        path: '/other',
        name: 'Other',
        component: () => import('../view/Other'),

    }


]



const router = new VueRouter({
    mode: "hash",
    routes
}
)

export default router